.video-container { 
    position: relative; 
    padding-bottom: 26.25%; 
    padding-top: 30px; 
    height: 0; 
    overflow: hidden; 
    max-width: 500px;
    max-height: 300px;
    margin: auto; 
    margin-bottom: 0.5rem
}

.video-container iframe, .video-container object, .video-container embed { 
    position: absolute; 
    top: 0; 
    left: 0; 
    width: 100%; 
    height: 100%; 
}